import * as zod from 'zod';

import { ens as name, id, string } from '../validationHelpers';

export const createEnsRecordSchema = zod.object({
  where: zod.object({ siteId: id, ipnsRecordId: id }),
  data: zod.object({ name }),
});

export const deleteEnsRecordSchema = zod.object({
  where: zod.object({ id }),
});

export const verifyEnsRecordSchema = zod.object({
  where: zod.object({ id }),
});

export const ensNameAvailabilitySchema = zod.object({
  where: zod.object({
    name,
    siteId: string.optional(),
  }),
});
