import { z, ZodError } from 'zod';
import { fromZodError } from 'zod-validation-error';

import { slug, string } from '../validationHelpers';

type ValidateConfigurationWithResultArgs = {
  configuration: object;
};

export const validateConfigurationWithResult = async ({ configuration }: ValidateConfigurationWithResultArgs) => {
  return fleekRootConfigSchema
    .parseAsync(configuration)
    .catch((error: ZodError) => Promise.reject(new Error(fromZodError(error).toString())));
};

export const fleekSiteConfigSchema = z
  .object({
    slug,
    distDir: string,
    buildCommand: string.optional(),
  })
  .strict();

export const fleekRootConfigSchema = z
  .object({
    sites: z.array(fleekSiteConfigSchema).length(1),
  })
  .strict();
