import * as zod from 'zod';

import { boolean, email, file, hostname, id, ipAddress, name, projectName, string, walletAddress } from '../validationHelpers';

export const usernameAvailabilitySchema = zod.object({
  where: zod.object({
    username: name,
  }),
});

export const emailAvailabilitySchema = zod.object({
  where: zod.object({
    email: email,
  }),
});

export const updateUserSchema = zod.object({
  data: zod.object({
    avatar: file.optional().nullable(),
    email: email.optional().nullable(),
    firstName: string.optional().nullable(),
    lastName: string.optional().nullable(),
    username: name.optional().nullable(),
    walletAddress: walletAddress.optional().nullable(),
    walletChain: string.optional().nullable(),
  }),
});

export const addUserToProjectSchema = zod.object({
  where: zod.object({
    projectId: id,
  }),
  data: zod.object({
    email,
    role: zod.enum(['OWNER', 'MEMBER']),
  }),
});

export const updateMembershipSchema = zod.object({
  where: zod.union([
    zod.object({
      id,
    }),
    zod.object({ userId: id }),
  ]),
  data: zod.object({
    permissionGroupId: id,
  }),
});

export const createProjectSchema = zod.object({
  data: zod.object({
    name: projectName,
    planId: id.optional(),
  }),
});

export const updateProjectSchema = zod.object({
  where: zod.object({
    id,
  }),
  data: zod.object({
    name: projectName.optional(),
    avatar: file.optional(),
    backupStorageOnArweave: boolean.optional(),
    backupStorageOnFilecoin: boolean.optional(),
    allowAccessFromOfacCountries: boolean.optional(),
    billingPlanId: id.optional(),
  }),
});

export const applicationNameAvailablitySchema = zod.object({
  where: zod.object({
    name,
  }),
});

// Warning: The following includes deprecated fields
// it's useful for retroactive support, which can "maybe" interop
// with whiteLabelDomains or whitelistDomains
export const createApplicationSchema = zod.object({
  data: zod.object({
    name, //
    // Note: validation can be messing retroactive support
    // for the whiteLabelDomains support, so set as optional temporarily
    whitelistDomains: zod.array(hostname.or(ipAddress)).optional(),
    // Warning: deprecated, used only for providing support for older clients
    whiteLabelDomains: zod.array(hostname.or(ipAddress)).optional(),
  }),
});

export const createApplicationSchemaNext = zod.object({
  data: zod.object({
    name, //
    whitelistDomains: zod.array(hostname.or(ipAddress)),
  }),
});

export const deleteApplicationSchema = zod.object({
  where: zod.object({
    id, //
  }),
});

// Warning: The following includes deprecated fields
// it's useful for retroactive support, which can "maybe" interop
// with whiteLabelDomains or whitelistDomains
export const updateApplicationSchema = zod.object({
  where: zod.object({
    id,
  }),
  data: zod.object({
    name: name.optional(),
    // Note: validation can be messing retroactive support
    // for the whiteLabelDomains support, so set as optional temporarily
    whitelistDomains: zod.array(hostname.or(ipAddress)).optional(),
    // Warning: deprecated, used only for providing support for older clients
    whiteLabelDomains: zod.array(hostname.or(ipAddress)).optional(),
  }),
});

export const updateApplicationSchemaNext = zod.object({
  where: zod.object({
    id,
  }),
  data: zod.object({
    name: name.optional(),
    whitelistDomains: zod.array(hostname.or(ipAddress)),
  }),
});

export const verifyTwoFactorSecretKey = zod.object({
  where: zod.object({
    secretKeyId: id,
  }),
  data: zod.object({
    token: string.length(6),
  }),
});

export const updateTwoFactorSecretKeySchema = zod.object({
  where: zod.object({
    id: id,
  }),
  data: zod.object({
    key: boolean.optional(),
    isActive: boolean.optional(),
  }),
});
