import * as zod from 'zod';

export const createBillingPlanSchema = zod.object({
  data: zod.object({
    name: zod.string(),
    version: zod.number().positive(),
    isActive: zod.boolean(),
    price: zod.number().positive(),
    billingPartnerId: zod.string().optional(),
    isPublicPlan: zod.boolean(),
    planLevel: zod.enum(['FREE', 'BASIC', 'PRO', 'ENTERPRISE']),
    description: zod.string(),
  }),
});

export const updateBillingPlanSchema = zod.object({
  where: zod.object({
    id: zod.string(),
  }),
  data: zod.object({
    isActive: zod.boolean().optional(),
    price: zod.number().positive().optional(),
  }),
});

export const createBillingCycleUsageSchema = zod.object({
  data: zod.object({
    billingCycleId: zod.string(),
    billingPlanOverageRuleId: zod.string(),
    amount: zod.number().positive(),
  }),
});
