/* eslint-disable no-process-env */
import { createAssetsAccessToken } from '@fleek-platform/utils-token';

export const getIpfsGatewayAssetsUrl = async (cid: string) => {
  const token = await createAssetsAccessToken({
    payload: {
      cid,
    },
    secret: process.env.SECRET_JWT_IDENTITY as string,
    expiration: '1m',
  });

  return `https://${process.env.ASSETS_URL as string}/cid?token=${token}`;
};
