import { isAddress } from 'viem';
import * as zod from 'zod';

const ETH_ADDRESS_REGEX = /^0x[a-fA-F0-9]{40}$/;
const NAME_REGEX = /^(?=[a-zA-Z0-9._\-~ ]*$)([^_.\-~ ].*[^_.\-~ ]|[^_.\-~ ])$/;
const IDENTIFIER_REGEX = /^[a-zA-Z0-9-]+$/;
const SLUG_REGEX = /^[a-z0-9]+(?:-[a-z0-9]+)*$/;
const ENV_VAR_NAME_REGEX = /^[A-Za-z][A-Za-z0-9_]*$/;
const HOSTNAME_REGEX = /^(?!-)[a-z0-9-]{1,63}(?<!-)(\.(?!-)[a-z0-9-]{1,63}(?<!-))+$/i;
const IP_ADDRESS_REGEX = /^(localhost|((\d{1,3}\.){3}\d{1,3}))(:\d+)?$/;
const ENS_REGEX = /^[a-z0-9.-]*\.eth$/i;
const PIN_REGEX = /^[A-Za-z0-9._\- ]+$/;
const UNIX_PATH_REGEX = /^(\/?(\.\/)?[\w.-]+\/?)*$/;
const EMAIL_REGEX =
  /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

export const any = zod.any(); // Not recommended
export const anyArray = zod.array(any); // Not recommended
export const anyRecord = zod.record(any); // Not recommended
export const enumeration = zod.enum; // Not recommended
export const boolean = zod.boolean();
export const string = zod.string();
export const number = zod.number();
export const positiveNumber = number.positive();
export const id = string;
export const ens = string.refine(
  (val) => {
    return ENS_REGEX.test(val);
  },
  (ens) => {
    return { message: `${ens} is an invalid ENS name` };
  }
);
export const walletAddress = string.refine(
  (address) => {
    return isAddress(address);
  },
  (address) => {
    return { message: `${address} is an invalid wallet address` };
  }
);
export const ethAddress = string.regex(ETH_ADDRESS_REGEX, { message: `Invalid Ethereum address` });
export const patName = string.min(3, { message: 'Minimum of 3 characters' }).max(50, { message: 'Maximum of 50 characters' });
export const pinName = string
  .min(1, { message: 'Minimum of 1 characters' })
  .max(30, { message: 'Maximum of 30 characters' })
  .regex(PIN_REGEX, { message: `Pin name only accept A-Z a-z 0-9 . _ -. ` })
  .refine((pinName) => !!pinName, { message: 'Pin name must not be blank' });
export const name = string
  .min(3, { message: 'Minimum of 3 characters' })
  .max(30, { message: 'Maximum of 30 characters' })
  .regex(NAME_REGEX, { message: `Name allows only letters, digits and not starting/ending spaces, '.', '-', and '~'` });
export const projectName = string
  .min(1, { message: 'Minimum of 1 characters' })
  .max(30, { message: 'Maximum of 30 characters' })
  .refine(
    (projectName) => {
      if (projectName) {
        return NAME_REGEX.test(projectName);
      }
    },
    { message: `Name allows only letters, digits and not starting/ending spaces, '.', '-', and '~'` }
  )
  .refine((porjectName) => !!porjectName, { message: 'Project name must not be blank' });
export const siteName = string
  .min(1, { message: 'Minimum of 1 characters' })
  .max(30, { message: 'Maximum of 30 characters' })
  .regex(IDENTIFIER_REGEX, { message: `Site Name allows only letters, digits and '-'` })
  .refine(
    (siteName) => {
      return !!siteName;
    },
    { message: 'Site name must not be blank' }
  );
export const functionName = string
  .min(3, { message: 'Minimum of 3 characters' })
  .max(30, { message: 'Maximum of 30 characters' })
  .regex(IDENTIFIER_REGEX, { message: `Function name allows only letters, digits and '-'` })
  .refine(
    (functionName) => {
      return !!functionName;
    },
    { message: 'Function name must not be blank' }
  );
export const privateGatewayName = string
  .min(3, { message: 'Minimum of 3 characters' })
  .max(30, { message: 'Maximum of 30 characters' })
  .regex(NAME_REGEX, { message: `Private Gateway Name allows only letters, digits and not starting/ending spaces, '.', '-', and '~'` });
export const email = string.regex(EMAIL_REGEX, { message: 'Invalid email' });
export const hostname = string
  .min(2, { message: 'Minimum of 2 characters' })
  .max(80, { message: 'Maximum of 80 characters' })
  .refine((hostName) => !!hostName, { message: 'domain name must not be blank' })
  .refine(
    (hostname) => HOSTNAME_REGEX.test(hostname),
    (hostname) => ({ message: `${hostname} is an invalid domain` })
  );

export const ipAddress = string.refine(
  (ipAddress) => IP_ADDRESS_REGEX.test(ipAddress),
  (ipAddress) => ({ message: `${ipAddress} isn't a valid IP address or a localhost with port number` })
);

export const description = string.max(500, { message: 'Maximum of 500 characters' });
export const slug = string
  .max(63, { message: 'Maximum of 63 characters' })
  .regex(SLUG_REGEX, { message: `Slug allows only lowercase letters, digits and '-'` });
export const envVarName = string
  .min(3, { message: 'Minimum of 3 characters' })
  .max(150, { message: 'Maximum of 150 characters' })
  .regex(ENV_VAR_NAME_REGEX, { message: 'Only letters and numbers allowed' });
export const envVarValue = string
  .min(1, { message: 'Minimum of 1 character' })
  .max(250, { message: 'Maximum of 250 characters' })
  .refine((value) => !!value, { message: `Value can't be null` });
export const framework = string.max(15, { message: 'Maximum of 15 characters' });
export const settingsInput = string.max(30, { message: 'Maximum of 30 characters ' });
export const dockerImage = string
  .min(3, { message: 'Minimum of 3 characters' })
  .max(50, { message: 'Maximum of 50 characters' })
  .refine(
    (dockerImage) => {
      return !!dockerImage;
    },
    { message: 'Docker image must not be blank' }
  );
export const url = string.url({ message: 'Invalid url' });
export const file = zod.any();
export const unixPath = string.regex(UNIX_PATH_REGEX, { message: `This is not a valid file path` });
export const functionStatus = zod.enum(['ACTIVE', 'INACTIVE']);
