import { getIpfsInternalGatewayUrl } from './getIpfsInternalGatewayUrl';

type StorageType = 'IPFS';
type GetInternalGatewayUrlByStorageTypeArgs = {
  storageType: StorageType;
  cid: string;
};

export const getInternalGatewayUrlByStorageType = ({ cid, storageType }: GetInternalGatewayUrlByStorageTypeArgs): string => {
  switch (storageType) {
    case 'IPFS':
      return getIpfsInternalGatewayUrl(cid);
    default:
      // eslint-disable-next-line fleek-custom/no-default-error
      throw new Error(`Cannot generate internal gateway URL for unknown storageType: ${storageType}`);
  }
};
