import * as zod from 'zod';

import { id, string } from '../validationHelpers';

export const publishSignedIpnsNameSchema = zod.object({
  data: zod.object({ input: string, key: string }),
});

export const resolveIpnsNameSchema = zod.object({
  where: zod.object({ name: string }),
});

export const publishIpnsRecordSchema = zod.object({
  where: zod.object({ id }),
  data: zod.object({ hash: string }),
});

export const deleteIpnsRecordSchema = zod.object({
  where: zod.object({ id }),
});

export const ipnsRecordSchema = zod.object({
  where: zod.object({ name: string }),
});
