import { ens_normalize } from '@adraffy/ens-normalize';
import * as zod from 'zod';

type IsEnsValidArgs = {
  name: string;
};

export const isEnsValid = ({ name }: IsEnsValidArgs) =>
  zod
    .string()
    .refine((val) => {
      try {
        return Boolean(ens_normalize(val));
      } catch (e) {
        return false;
      }
    })
    .safeParse(name).success;
