import { MissingExpectedDataError } from '@fleek-platform/errors';

import { getDefined } from './defined';

export const SITE_SLUG_DOMAIN = getDefined('SITE_SLUG_DOMAIN');

export const getFleekDefaultGatewayBySlug = ({ slug }: { slug: string }) => {
  if (!slug) {
    throw new MissingExpectedDataError();
  }

  return `https://${slug}.${SITE_SLUG_DOMAIN}`;
};

export * from './cache';
