import { stringify } from 'yaml';

// TODO: Decide where to place sites utils
export type GenerateWorkflowStepsArgs = GenerateDeploymentWorkflowYamlArgs & { nodeVersion: number };

export const generateWorkflowSteps = (args: GenerateWorkflowStepsArgs) => {
  const steps = [
    { name: 'Checkout', uses: 'actions/checkout@v3' },
    { name: 'Install Node.js', uses: 'actions/setup-node@v3', with: { 'node-version': args.nodeVersion } },
    { name: 'Install Fleek CLI', run: 'npm i -g @fleek-platform/cli' },
  ];
  const deployStep = { name: 'Build & deploy sites', run: 'fleek sites deploy' };

  if (args.installCommand) {
    steps.push({ name: 'Install Packages', run: args.installCommand });
  }

  if (args.fleekConfigPath) {
    deployStep.run = `fleek sites deploy --config ${args.fleekConfigPath}`;
  }

  steps.push(deployStep);

  return steps;
};

export type GenerateDeploymentWorkflowYamlArgs = {
  nodeVersion: number;
  fleekConfigPath?: string;
  installCommand?: string;
};

export const generateDeploymentWorkflowYaml = (args: GenerateDeploymentWorkflowYamlArgs): string => {
  const yamlLikeObject = {
    name: 'Deploy site via Fleek',
    on: 'push',
    jobs: {
      'deploy-to-fleek': {
        'runs-on': 'ubuntu-latest',
        env: {
          FLEEK_TOKEN: '${{ secrets.FLEEK_TOKEN }}',
          FLEEK_PROJECT_ID: '${{ secrets.FLEEK_PROJECT_ID }}',
        },
        steps: generateWorkflowSteps(args),
      },
    },
  };

  return stringify(yamlLikeObject);
};
