import * as zod from 'zod';

import { id, name, string } from '../validationHelpers';

export const updatePinSchema = zod.object({
  where: zod.object({
    id: string,
  }),
  data: zod.object({
    filename: name.optional(),
    parentFolderId: id.optional(),
    moveToRoot: zod.literal(true).optional(),
  }),
});

export const deletePinSchema = zod.object({
  where: zod.object({
    id: string,
  }),
});

export const presignPinUrlSchema = zod.object({
  data: zod.object({
    basename: string,
    format: zod.enum(['car', 'raw']),
    cid: string.optional(),
  }),
});

export const completePinUploadSchema = zod.object({
  where: zod.object({
    key: string,
  }),
});

export const createFolderSchema = zod.object({
  where: zod.object({ parentFolderId: id.optional() }),
  data: zod.object({ name }),
});

export const updateFolderSchema = zod.object({
  where: zod.object({ id }),
  data: zod.object({ name }),
});

export const deleteFolderSchema = zod.object({
  where: zod.object({ id }),
});
