import { FleekError } from './FleekError';

type ProjectSiteLimitExceededErrorOptions = Record<string, never>;

export class ProjectSiteLimitExceededError extends FleekError<ProjectSiteLimitExceededErrorOptions> {
  public name = 'ProjectSiteLimitExceededError';

  constructor() {
    super({});
  }

  toString = () => `You've reached the maximum number of sites for this project. Contact our Support team if you have any questions.`;
}
