import * as fs from 'fs';
import path from 'path';

type IsFunctionPathValidArgs = {
  fileOrFolderPath: string;
};

export const validExtension = new Set(['.js', '.mjs', '.cjs', '.ts', '.jsx', '.tsx', '.wasm']);

export const isValidFolder = async (path: string) => {
  try {
    if (fs.lstatSync(path).isDirectory()) {
      return true;
    }

    return false;
  } catch (err) {
    return false;
  }
};

export const isFunctionPathValid = async ({ fileOrFolderPath }: IsFunctionPathValidArgs) => {
  return fs.existsSync(fileOrFolderPath) && (validExtension.has(path.extname(fileOrFolderPath)) || (await isValidFolder(fileOrFolderPath)));
};
