// TODO: Deprecate in favour of utils version
import { MissingExpectedDataError } from '@fleek-platform/errors';
import CID from 'cids';

import { getDefined } from '../defined';

export const IPFS_GATEWAY_HOSTNAME = getDefined('IPFS_GATEWAY_HOSTNAME');

export const getIpfsInternalGatewayUrl = (hash: string) => {
  const cid = new CID(hash);
  const cidV1 = cid.toV1().toString('base32');

  if (!IPFS_GATEWAY_HOSTNAME) {
    throw new MissingExpectedDataError();
  }

  return `https://${cidV1}.ipfs.${IPFS_GATEWAY_HOSTNAME}`;
};
