import * as zod from 'zod';

import { boolean, id, name } from '../validationHelpers';

export const updatePrivateGatewaySchema = zod.object({
  where: zod.object({ id }),
  data: zod.object({ name: name.optional(), blockOfacCountries: boolean.optional() }),
});

export const createPrivateGatewaySchema = zod.object({
  where: zod.object({ zoneId: id }),
  data: zod.object({ name }),
});

export const deletePrivateGatewaySchema = zod.object({
  where: zod.object({ id }),
});

export const privateGatewaySchema = zod.object({
  where: zod.object({ id }),
});

export const privateGatewayNameAvailabilitySchema = zod.object({
  where: zod.object({
    name: name,
  }),
});
