import * as zod from 'zod';

import { email } from '../validationHelpers';

export const createSalesContactRequestSchema = zod.object({
  data: zod.object({
    email,
    description: zod.string().min(10, { message: 'Minimum of 10 characters' }).max(1000, { message: 'Maximum of 1000 characters' }),
  }),
});
