import * as zod from 'zod';

import { dockerImage, envVarName, file, id, siteName, slug, string, unixPath } from '../validationHelpers';

export const createSiteSchema = zod.object({
  data: zod.object({
    name: siteName,
    sourceProvider: zod.enum(['GITHUB', 'GITLAB', 'BITBUCKET']).optional(),
    sourceRepositoryId: zod.string().optional(),
    sourceRepositoryOwner: zod.string().optional(),
    sourceRepositoryName: zod.string().optional(),
    sourceBranch: zod.string().optional(),
    enablePreviews: zod.boolean().optional(),
    baseDirectory: unixPath.optional(),
    buildCommand: zod.string().optional(),
    distDirectory: unixPath.optional(),
    dockerImage: dockerImage.optional(),
    cpuLimit: zod.string().optional(),
    memoryLimit: zod.string().optional(),
    githubInstallationId: zod.number().positive().optional(),
    gitIntegrationId: id.optional(),
    buildDurationLimitSeconds: zod.number().positive().optional(),
    templateId: id.optional(),
    frameworkId: id.optional(),
  }),
});

// TODO: more strict validation for fields like sourceBranch
export const updateSiteSchema = zod.object({
  where: zod.object({
    id,
  }),
  data: zod.object({
    name: siteName.optional(),
    slug: slug.optional(),
    sourceProvider: zod.enum(['GITHUB', 'GITLAB', 'BITBUCKET']).optional(),
    sourceRepositoryId: zod.string().optional(),
    sourceRepositoryOwner: zod.string().optional(),
    sourceRepositoryName: zod.string().optional(),
    sourceBranch: zod.string().optional(),
    enablePreviews: zod.boolean().optional(),
    baseDirectory: unixPath.optional(),
    buildCommand: zod.string().optional(),
    distDirectory: unixPath.optional(),
    dockerImage: dockerImage.optional(),
    cpuLimit: zod.string().optional(),
    memoryLimit: zod.string().optional(),
    githubInstallationId: zod.number().positive().optional(),
    buildDurationLimitSeconds: zod.number().positive().optional(),
    avatar: file.optional(),
    frameworkId: id.nullish(),
  }),
});

export const createCustomIpfsDeploymentSchema = zod.object({
  data: zod.object({
    siteId: id,
    cid: string,
  }),
});

export const createSecretSchema = zod.object({
  data: zod.object({ groupId: id, key: string, value: string, visibility: zod.enum(['PUBLIC', 'ENCRYPTED']) }),
});

export const updateSecretSchema = zod.object({
  where: zod.object({ id }),
  data: zod.object({ value: string, visibility: zod.enum(['PUBLIC', 'ENCRYPTED']) }),
});

export const deleteSecretSchema = zod.object({
  where: zod.object({ id }),
});

export const slugAvailabilitySchema = zod.object({
  where: zod.object({ slug }),
});

export const siteNameAvailabilitySchema = zod.object({
  where: zod.object({
    name: siteName,
  }),
});

export const secretAvailabilitySchema = zod.object({
  where: zod.object({
    siteId: id,
    key: envVarName,
  }),
});
