import * as zod from 'zod';

import { hostname, id } from '../validationHelpers';

export const createDomainSchema = zod.object({
  where: zod.object({ zoneId: id }),
  data: zod.object({ hostname }),
});

export const createDnsConfigSchema = zod.object({
  where: zod.object({
    domainId: id,
    siteId: id,
  }),
  data: zod.object({
    name: zod.enum(['_dnslink']),
  }),
});

export const createZoneForSiteSchema = zod.object({
  where: zod.object({ siteId: id }),
});

export const deleteZone = zod.object({
  where: zod.object({ id }),
});

export const domainAvailabilitySchema = zod.object({
  where: zod.object({
    hostname,
  }),
});
