import * as zod from 'zod';

import { description, file, id, name } from '../validationHelpers';

export const createTemplateSchema = zod.object({
  data: zod.object({
    name,
    description,
    siteId: id,
    templateCategoryId: id,
    banner: file,
  }),
});

export const updateTemplateSchema = zod.object({
  where: zod.object({
    id,
  }),
  data: zod.object({
    name: name.nullish().or(zod.literal('')),
    description: description.nullish(),
    templateCategoryId: id.nullish(),
    banner: file.optional(),
  }),
});

export const deleteTemplateSchema = zod.object({
  where: zod.object({
    id,
  }),
});

export const templateNameAvailabilitySchema = zod.object({
  where: zod.object({
    name,
  }),
});
